import React, { Component } from 'react'
import Preloader from '../../../components/Preloader/Preloader'
import { checkAddTagForm } from '../../Validations/tagsValidation'
import { updateTag } from '../../FirebaseService/tagsMethods'
import { connect } from 'react-redux'
import { setCurrentModal, setShure } from '../../../actions/modalServiceActions'
import { SketchPicker } from 'react-color';
import firebase from '../../../firebase';
import Button from '../../../components/BasicUi/Button'

class UpdateTagModal extends Component {
  state = {
    name: { value: '', isValid: false },
    color: { value: '', isValid: false },
    errors: [],
    loading: false,
    checked: false,
    showPicker: false,
    tagsRef: firebase.database().ref('tags'),
    chunksRef: firebase.database().ref('chunks')
  }
  componentDidMount() {
    const { updatingTag } = this.props
    this.setState({
      name: { value: updatingTag.name, isValid: false },
      color: { value: updatingTag.color, isValid: false },
      tagId: updatingTag.id
    })
  }

  handleChange = e => {
    const el = { ...this.state[e.target.name] }
    el.value = e.target.value
    this.setState({ [e.target.name]: el })
  }

  setStateCurrent = (propName, value) => {
    this.setState({
      [propName]: value
    })
  }
  
  handleSubmit = e => {
    e.preventDefault()
    if (checkAddTagForm(this.state, this.setStateCurrent)) {
      const { name, color, tagId } = this.state
      const { user, setCurrentModal, chunks } = this.props
      let filteredChunks = chunks.filter(el => el.tags.find(tag => tag.id === tagId))
      filteredChunks.forEach(chunk => {
        
        chunk.tags.map(tag2 => {

          if(tag2.id === tagId) {
            tag2.label = name.value
            tag2.value = name.value
            tag2.color = color.value
          }
        })
        // chunk.tags = newTags.length ? newTags : null
      })
      //const chunksRef = firebase.database().ref('chunks')
      updateTag(name.value, color.value, user.uid, setCurrentModal, tagId, filteredChunks)
    }
      
  }
  handleChangeComplete = color => {
    const colorNew = {...this.state.color}
    colorNew.value = color.hex
    this.setState({ color: colorNew });
  }
  deleteClick = () => {
    const { name, color, tagId } = this.state
    const { user, setCurrentModal, chunks } = this.props
    // this.props.setShure(['deleteTag', this.state.tagsRef.child(`${id}`)])
    // this.props.setCurrentModal('areYouShureModal')
    // this.state.chunksRef.child(`${this.state.chunk.id}`).remove().then(() => {
    //   this.props.history.push('/chunks')
    // })
    let filteredChunks = chunks.filter(el => el.tags.find(tag => tag.id === tagId))
      if(filteredChunks.length === 1 && filteredChunks[0].tags.length === 1) {
        this.setState({ cantDelete: true })
        return null
      } else {
        this.setState({ cantDelete: false })
      }
        
      filteredChunks.forEach(chunk => {
        const idx = chunk.tags.findIndex(el => el.id === tagId)
        chunk.tags.splice(idx,1)

      })
    //console.log('deleted chunks', filteredChunks)
    updateTag(null, null, user.uid, setCurrentModal, tagId, filteredChunks, 'deleteThisChunk')

  }
  render() {
    const { name, color, errors, checked, loading, showPicker } = this.state
    return (
      <div className="formblock add-tag">
        <div className="title">Update Tag</div>

        <form onSubmit={this.handleSubmit}>
          <div className="inputs">
            <div className="item">
              <div className="icon"><i className="fa fa-info" aria-hidden="true"></i></div>
              <input autoComplete='off' className={name.isValid || !checked ? '' : 'wrong'} onChange={this.handleChange} value={name.value} type="text" placeholder='Abbreviation' name='name' />
            </div>
            <div className="item">
              <div className="icon"><i className="fa fa-flask" aria-hidden="true" style={{color: `${color.value}`}}></i></div>
              <input autoComplete='off' className={color.isValid || !checked ? '' : 'wrong'} style={{color: `${color.value}`}} onFocus={() => this.setState({showPicker: true})}  onChange={this.handleChange} value={color.value} type="text" placeholder='HEX Color' name='color' />
            </div>
            { showPicker ? <SketchPicker
              color={ color.value }
              onBlur={() => this.setState({showPicker: false})}
              onChangeComplete={ this.handleChangeComplete }
            /> : null }
           
          </div>

          {errors.length > 0 ? <div className='errors'>{errors.map((el,idx) => <div className='item' key={`${el}+${idx}`}> -- {el}-- </div>)}</div> : null}

          {/* <div className="buttons">
            <Button loading={loading} green>Update</Button>
            <Button loading={loading} red>Delete</Button>
          </div> */}
          <div className="buttons-wrapper" style={{ display: 'flex' }}>
            <button disabled={loading} type="submit" className="btn-1">{loading ? <Preloader white/> : 'Update'}</button>
            {/* <Button red style={{ display: 'flex', marginLeft: '10px', height: 'auto', fontSize: '1.5rem' }} onClick={this.deleteClick}>{this.state.cantDelete ? `!` : <i className="fa fa-trash" aria-hidden="true"></i>}</Button> */}
          </div>
          
          {/* <button disabled={loading} type="submit" className="btn-1">{loading ? <Preloader white/> : 'Update'}</button> */}

        </form>
      </div>
    )
  }
}
const mapStateToProps = state => ({
  user: state.user.currentUser,
  updatingTag: state.tags.updatingTag,
  chunks: state.chunks.chunks
})
export default connect(mapStateToProps, {setCurrentModal, setShure})(UpdateTagModal)