export const checkChunkPageForm = (chunk, setState) => {
  !chunk.tags.length ? setState({ tagsValid: false, isChecked: true }) : setState({ tagsValid: true, isChecked: true })
  !chunk.name ? setState({ nameValid: false, isChecked: true }) : setState({ nameValid: true, isChecked: true })
  !chunk.des ? setState({ desValid: false, isChecked: true }) : setState({ desValid: true, isChecked: true })
  !chunk.content ? setState({ codeValid: false, isChecked: true }) : setState({ codeValid: true, isChecked: true })

  if(!chunk.tags.length || !chunk.name || !chunk.des || !chunk.content) {
    
    return false
  } else {
    setState({ isChecked: false })
    return true
  }
}