import React, { Component } from 'react';
import Header from './components/Header/Header'
import Chunks from './components/Chunks/Chunks';
import ChunkPage from './components/ChunkPage/ChunkPage';
import TagsPage from './components/TagsPage/TagsPage';
import { Switch, Route, Redirect } from 'react-router-dom'
import ModalService from './_services/ModalService/ModalService';


class App extends Component {
  
  render() {
    return (
      <React.Fragment>
        <Header />
        <Switch>
          <Route exact path='/chunks' component={Chunks} />
          <Route exact path='/chunks/:id' component={ChunkPage} />
          <Route exact path='/tags' component={TagsPage} />
          <Route exact path='/tags/:id' component={TagsPage} />
          <Redirect exact from='/' to='/chunks' />
        </Switch>
        <ModalService history={this.props.history}/>
      </React.Fragment>
    )
  }
}

export default App;
