import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import './headerNav.scss'


const HeaderNav = ({ currentHeaderNavItem, isLoadingChunks, isLoadingTags }) => {
  return (
    <div className='header-nav'>
      {isLoadingChunks || isLoadingTags ? null : 
      <React.Fragment>
        <Link to='/chunks' className={currentHeaderNavItem === 'chunks' ? 'selected' : ''}>Chunks</Link>
        <Link to='/tags' className={currentHeaderNavItem === 'tags' ? 'selected' : ''}>Tags</Link> 
      </React.Fragment>
      }
    </div>
  )
}
const mapStateToProps = state => ({
  currentHeaderNavItem: state.header_nav.currentHeaderNavItem,
  isLoadingChunks: state.chunks.isLoading,
  isLoadingTags: state.tags.isLoading,
})

export default connect(mapStateToProps)(HeaderNav)