import * as validations from './validations'
export const checkLoginForm = (state, setStateLogin) => {
  const errors = []

  const emailProp = { ...state.email }
  const passProp = { ...state.pass }

  setStateLogin('checked', true)

  emailProp.isValid = validations.checkEmail(errors, state)
  passProp.isValid = validations.checkPass(errors, state, 'login')

  setStateLogin('email', emailProp)
  setStateLogin('pass', passProp)

  if (isLoginFormEmpty(errors, state)) {
    setStateLogin('errors', errors)
    return false
  }

  if (emailProp.isValid && passProp.isValid) {
    setStateLogin('errors', [])
    return true
  }

  setStateLogin('errors', errors)

  return false
}
const isLoginFormEmpty = (errors, { email, pass }) => {
  if (email.value.length === 0 && pass.value.length === 0) {
    errors.push('Form is empty !')
    return true
  } else {
    return false
  }
}