import * as validations from './validations'
export const checkRegisterForm = (state, setStateRegister) => {
  const errors = []

  const userProp = { ...state.username }
  const emailProp = { ...state.email }
  const passProp = { ...state.pass }

  setStateRegister('checked', true)

  userProp.isValid = validations.checkUsername(errors, state)
  emailProp.isValid = validations.checkEmail(errors, state)
  passProp.isValid = validations.checkPass(errors, state)

  setStateRegister('username', userProp)
  setStateRegister('email', emailProp)
  setStateRegister('pass', passProp)
  setStateRegister('errors', errors)

  if (isRegisterFormEmpty(errors, state)) {
    setStateRegister('errors', errors)
    return false
  }

  if (userProp.isValid && emailProp.isValid && passProp.isValid) { return true }

  return false
}
const isRegisterFormEmpty = (errors, { username, email, pass, passConf }) => {
  if (!username.value.length && !email.value.length && !pass.value.length && !passConf.value.length) {
    errors.push('Form is empty !')
    return true
  } else {
    return false
  }
}