import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import App from './App';
import Login from './components/Auth/Login/Login'
import Register from './components/Auth/Register/Register'
import { BrowserRouter as Router, Switch, Route, withRouter } from 'react-router-dom'

import { createStore } from 'redux'
import { Provider, connect } from 'react-redux'
import { composeWithDevTools } from 'redux-devtools-extension'
import rootReducer from './reducers'
import { PreloaderApp } from './components/Preloader/Preloader';
import { authUser } from './_services/FirebaseService/authMethods';
import { setUser, clearUser } from './actions/authActions';

const store = createStore(rootReducer, composeWithDevTools())

class Root extends React.Component {
  componentDidMount() {
    const { history, setUser, clearUser } = this.props
    authUser(history, setUser, clearUser)
  }
  render() {
    const { isLoading, currentUser } = this.props
    return isLoading ? <PreloaderApp/> : (
      <Switch>
        {currentUser ? <Route path='/' component={App} /> : null}
        <Route exact path='/login' component={Login} />
        <Route exact path='/register' component={Register} />
      </Switch>
    )
  }
}

const mapStateToProps = state => ({
  isLoading: state.user.isLoading,
  currentUser: state.user.currentUser
})

const RouterWithAuth = withRouter(connect(mapStateToProps, { setUser, clearUser })(Root))

ReactDOM.render(<Provider store={store}><Router><RouterWithAuth /></Router></Provider>, document.getElementById('root'));

