import { combineReducers } from 'redux'
import { user_reducer } from './authReducer'
import { header_nav_reducer } from './headerReducer'
import { tags_reducer } from './tagsReducer';
import { chunks_reducer } from './chunksReducer';
import { modal_service_reducer } from './modalServiceReducer';
import { search_reducer } from './searchReducer';

const rootReducer = combineReducers({
  user: user_reducer,
  header_nav: header_nav_reducer,
  tags: tags_reducer,
  chunks: chunks_reducer,
  modal_service: modal_service_reducer,
  search: search_reducer

})

export default rootReducer