export const checkAddTagForm = (state, setState) => {
  const errors = []

  const nameProp = { ...state.name }
  const colorProp = { ...state.color }

  setState('checked', true)

  nameProp.isValid = checkName(errors, state)
  colorProp.isValid = checkColor(errors, state)

  setState('name', nameProp)
  setState('color', colorProp)

  if (isFormEmpty(errors, state)) {
    setState('errors', errors)
    return false
  }

  if (nameProp.isValid && colorProp.isValid) {
    setState('errors', [])
    return true
  }

  setState('errors', errors)

  return false
}
export const isFormEmpty = (errors, { name, color }) => {
  if (name.value.length === 0 && color.value.length === 0) {
    errors.push('Form is empty !')
    return true
  } else {
    return false
  }
}
export const checkName = (errors, { name }) => {
  if (name.value.length < 1) {
      errors.push('Name of Tag must contain at least 1 symbols')
      return false
    }
    else {
      return true
    }
}
export const checkColor = (errors, { color }) => {
  const regExp = /^#[0-9a-f]{6}$/;
  if(color.value.match(regExp)){
    return true
  } else {
    errors.push('Invalid HEX color format')
    return false
  }
}
