import * as actionTypes from '../actions/types'

const initialState = {
  tags: [],
  currentTag: {id: null, value: 'all'},
  isLoading: true,
  updatingTag: null
}

export const tags_reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_TAGS:
      return {
        ...state,
        tags: action.payload.tags,
        isLoading: false,
  
      }
    case actionTypes.SET_CURRENT_TAG:
      return {
        ...state,
        currentTag: action.payload.currentTag
      }
    case actionTypes.SET_UPDATING_TAG:
      return {
        ...state,
        updatingTag: action.payload.updatingTag
      }
    default: 
      return state
  }
}

