import * as actionTypes from '../actions/types'

const initialState = {
  currentModal: null,
  shureData: null
}

export const modal_service_reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_CURRENT_MODAL:
      return {
        ...state,
        currentModal: action.payload.currentModal,
      }
    case actionTypes.SET_SHURE:
      return {
        ...state,
        shureData: action.payload.shureData,
      }
    default: 
      return state
  }
}

