export const checkPass = (errors, { pass, passConf }, type) => {
  if (type === 'login') {
    if (pass.value.length < 6) {
      errors.push('Password must contain at least 6 symbols')
      return false
    }
  } else {
    if (pass.value.length < 6 || passConf.value.length < 6) {
      errors.push('Password must contain at least 6 symbols')
      return false
    }
    if (pass.value !== passConf.value) {
      errors.push('Passwords not equal')
      return false
    }
  }

  return true

}

export const checkUsername = (errors, { username }) => {
  if (username.value.length < 4) {
    errors.push('Username must contain at least 4 symbols')
    return false
  }
  else {
    return true
  }
}
export const checkEmail = (errors, { email }) => {
  if (email.value.length < 4) {
    errors.push('Email must contain at least 4 symbols')
    return false
  }
  else {
    return true
  }
}