import React, { Component } from 'react'
import { connect } from 'react-redux'
import { setSearchStr } from '../../../actions/searchActions';
import './searchBox.scss'

class SearchBox extends Component {
  state = {
    inputValue: ''
  }
  setPlaceholder = () => {
    const { currentHeaderNavItem } = this.props
    let placeholder = ''
    switch (currentHeaderNavItem) {
      case ('chunks'): placeholder = 'Find your chunk...'; break
      case ('tags'): placeholder = 'Find your tag...'; break
      default: placeholder = 'Search works only on chunks or tags page'
    }
    return placeholder
  }
  onChange = e => {
    this.setState({ inputValue: e.target.value })
  }
  componentDidUpdate(prevProps, prevState) {
    if(prevState.inputValue !== this.state.inputValue) {
      this.props.setSearchStr(this.state.inputValue)
    }
  }
  render() {
    return (
      <div className='search-box'>
        <input type="text" placeholder={this.setPlaceholder()} onChange={this.onChange} value={this.props.searchStr} />
        <i className="fa fa-search" aria-hidden="true"></i>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  currentHeaderNavItem: state.header_nav.currentHeaderNavItem,
  searchStr: state.search.searchString
})
export default connect(mapStateToProps, { setSearchStr })(SearchBox)

