export const checkAddChunkForm = (state, setState) => {
  const errors = []

  const nameProp = { ...state.name }
  const desProp = { ...state.des }
  const langProp = { ...state.language }
  const tagsProp = { ...state.selectedTags }
  const codeProp = { ...state.code }

  setState('checked', true)

  nameProp.isValid = checkName(errors, state)
  desProp.isValid = checkDes(errors, state)
  langProp.isValid = checkLang(errors, state)
  tagsProp.isValid = checkTags(errors, state)
  codeProp.isValid = checkCode(errors, state)

  setState('name', nameProp)
  setState('des', desProp)
  setState('language', langProp)
  setState('selectedTags', tagsProp)
  setState('code', codeProp)

  if (isFormEmpty(errors, state)) {
    setState('errors', errors)
    return false
  }

  if (nameProp.isValid && desProp.isValid && langProp.isValid && tagsProp.isValid && codeProp.isValid) {
    setState('errors', [])
    return true
  }

  setState('errors', errors)

  return false
}
export const isFormEmpty = (errors, { name, des, language, selectedTags, code }) => {
  if (name.value.length === 0 && des.value.length === 0 && language.value.length === 0 && selectedTags.value.length === 0 && code.value.length === 0) {
    errors.push('Form is empty !')
    return true
  } else {
    return false
  }
}
export const checkName = (errors, { name }) => {
  if (name.value.length < 1) {
      errors.push('Name of Chunk must contain at least 1 symbols')
      return false
    }
    else {
      return true
    }
}
export const checkDes = (errors, { des }) => {
  if (des.value.length < 1) {
      errors.push('Description of Chunk must contain at least 1 symbols')
      return false
    }
    else {
      return true
    }
}
export const checkLang = (errors, { language }) => {
  if (language.value.length < 1) {
      errors.push('Language of Chunk must contain at least 1 symbols')
      return false
    }
    else {
      return true
    }
}
export const checkTags = (errors, { selectedTags }) => {
  if (selectedTags.value.length < 1) {
      errors.push('Must be at least 1 tag')
      return false
    }
    else {
      return true
    }
}
export const checkCode = (errors, { code }) => {
  if (code.value.length < 1) {
      errors.push('Code of Chunk must contain at least 1 symbols')
      return false
    }
    else {
      return true
    }
}
