import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import firebase from '../../../firebase';
import Preloader from '../../Preloader/Preloader';
import { createUser } from '../../../_services/FirebaseService/authMethods';
import { checkRegisterForm } from '../../../_services/Validations/registerFormValidation'
import '../auth.scss'
import { withRouter } from 'react-router-dom'

class Register extends Component {

  state = {
    username: { value: '', isValid: false },
    email: { value: '', isValid: false },
    pass: { value: '', isValid: false },
    passConf: { value: '', isValid: false },
    errors: [],
    loading: false,
    checked: false,
    userRef: firebase.database().ref('users')
  }

  handleChange = e => {
    const el = { ...this.state[e.target.name] }
    el.value = e.target.value
    this.setState({ [e.target.name]: el })
  }

  setStateRegister = (propName, value) => {
    this.setState({
      [propName]: value
    })
  }
  
  handleSubmit = e => {
    e.preventDefault()
    if (checkRegisterForm(this.state, this.setStateRegister)) {
      createUser(this.state, this.setStateRegister, this.saveUser, this.props.history)
    }
  }

  saveUser = createdUser => {
    return this.state.userRef.child(createdUser.user.uid).set({
      name: createdUser.user.displayName,
      avatar: createdUser.user.photoURL
    })
  }

  render() {
    const { username, email, pass, passConf, errors, checked, loading } = this.state
    return (
      <div className='auth-component-wrapper register'>
        <div className="formblock register">
          <div className="title">Register</div>
          <div className="des">ChunkBag register</div>

          <form onSubmit={this.handleSubmit}>
            <div className="inputs">
              <div className="item">
                <div className="icon"><i className="fa fa-user" aria-hidden="true"></i></div>
                <input className={username.isValid || !checked ? '' : 'wrong'} type="text" placeholder='Username' onChange={this.handleChange} value={username.value} name='username' />
              </div>
              <div className="item">
                <div className="icon"><i className="fa fa-envelope-o" aria-hidden="true"></i></div>
                <input className={email.isValid || !checked ? '' : 'wrong'} type="text" placeholder='Email' onChange={this.handleChange} value={email.value} name='email' />
              </div>
              <div className="item">
                <div className="icon"><i className="fa fa-lock" aria-hidden="true"></i></div>
                <input className={pass.isValid || !checked ? '' : 'wrong'} type="password" placeholder='Password' onChange={this.handleChange} value={pass.value} name='pass' />
              </div>
              <div className="item">
                <div className="icon"><i className="fa fa-lock" aria-hidden="true"></i></div>
                <input className={pass.isValid || !checked ? '' : 'wrong'} type="password" placeholder='Password Confirmation' onChange={this.handleChange} value={passConf.value} name='passConf' />
              </div>
            </div>

            {errors.length > 0 ? <div className='errors'>{errors.map(el => <div className='item' key={el}> -- {el}-- </div>)}</div> : null}

            <button disabled={loading} type="submit" className="btn-1">{loading ? <Preloader white/> : 'Go'}</button>

            <div className="donthave">Already a user? <Link to='/login'>Sign In</Link></div>
          </form>
        </div>
      </div>
    )
  }
}
export default withRouter(Register)