import md5 from 'md5'
import firebase from '../../firebase'


export const createUser = (state, setStateRegister, saveUser, history) => {
  setStateRegister('loading', true)
  firebase
    .auth()
    .createUserWithEmailAndPassword(state.email.value, state.pass.value)
    .then(createdUser => {
      console.log(createdUser)
      createdUser.user.updateProfile({
        displayName: state.username.value,
        photoURL: `http://gravatar.com/avatar/${md5(createdUser.user.email)}?d=identicon`
      })
        .then(() => {
          saveUser(createdUser).then(() => {
            console.log('user saved')
            setStateRegister('loading', false)
            history.push('/')
          })
        })
        .catch(error => { _pushError(state, error, setStateRegister) })
    })
    .catch(error => { _pushError(state, error, setStateRegister) })
}

export const signInUser = (state, setStateLogin) => {
  setStateLogin('loading', true)
  

  firebase
    .auth()
    .signInWithEmailAndPassword(state.email.value, state.pass.value)
    .then(signedInUser => {
      console.log(signedInUser, 'signedInUser')
    })
    .catch(error => { _pushError(state, error, setStateLogin) })
}
export const authUser = (history, setUser, clearUser) => {
  firebase.auth().onAuthStateChanged(user => {
    if (user) {
      console.log(user)
      setUser(user)
      history.push('/')
    } else {
      clearUser()
      history.push('/login')
    }
  })
}

// sign out 
export const handleSignOut = () => {
  firebase.auth().signOut().then(() => console.log('signed out'))
  //window.location.reload()
}

const _pushError = (state, error, setState) => {
  console.log(error);
  setState('loading', false)
  if (!state.errors.find(el => el === error.message))
    setState('errors', [error.message])
}