import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { checkLoginForm } from '../../../_services/Validations/loginFormValidation'
import Preloader from '../../Preloader/Preloader';
import { signInUser } from '../../../_services/FirebaseService/authMethods';
import '../auth.scss'

export default class Login extends Component {

  state = {
    email: { value: '', isValid: false },
    pass: { value: '', isValid: false },
    errors: [],
    loading: false,
    checked: false,
  }

  handleChange = e => {
    const el = { ...this.state[e.target.name] }
    el.value = e.target.value
    this.setState({ [e.target.name]: el })
  }

  setStateLogin = (propName, value) => {
    this.setState({
      [propName]: value
    })
  }
  
  handleSubmit = e => {
    e.preventDefault()
    if (checkLoginForm(this.state, this.setStateLogin)) {
      signInUser(this.state, this.setStateLogin)
    }
      
  }

  render() {
    const { email, pass, errors, checked, loading } = this.state
    return (
      <div className='auth-component-wrapper register'>
        <div className="formblock register">
          <div className="title">Login</div>
          <div className="des">ChunkBag Login</div>

          <form onSubmit={this.handleSubmit}>
            <div className="inputs">
              <div className="item">
                <div className="icon"><i className="fa fa-envelope-o" aria-hidden="true"></i></div>
                <input className={email.isValid || !checked ? '' : 'wrong'} type="text" placeholder='Email' onChange={this.handleChange} value={email.value} name='email' />
              </div>
              <div className="item">
                <div className="icon"><i className="fa fa-lock" aria-hidden="true"></i></div>
                <input className={pass.isValid || !checked ? '' : 'wrong'} type="password" placeholder='Password' onChange={this.handleChange} value={pass.value} name='pass' />
              </div>
            </div>

            {errors.length > 0 ? <div className='errors'>{errors.map((el,idx) => <div className='item' key={`${el}+${idx}`}> -- {el}-- </div>)}</div> : null}

            <button disabled={loading} type="submit" className="btn-1">{loading ? <Preloader white/> : 'Go'}</button>

            <div className="donthave">Don't have an account? <Link to='/Register'>Sign Up</Link></div>
          </form>
        </div>
      </div>
    )
  }
}
