import React from 'react'
import Button from '../../../components/BasicUi/Button'
import { connect } from 'react-redux'
import { setCurrentModal } from '../../../actions/modalServiceActions'

const AreYouShureModal = (props) => {
  const yes = () => {
    props.shureData[1].remove().then(() => {
      props.shureData[0] === 'deleteTag' ? props.history.push('/tags') : props.history.push('/chunks')
      props.setCurrentModal(null)
    })
  }
  const no = () => {
    props.setCurrentModal(null)
  }
  return (
    <div className='formblock shure'>
      <div className="title">Are you shure?</div>
      <div className="buttons">
        <Button onClick={yes} green >Yes</Button>
        <Button onClick={no} red >No</Button>
      </div>
    </div>
  )
}
const mapStateToProps = state => ({
  shureData: state.modal_service.shureData
})
export default connect(mapStateToProps, { setCurrentModal })(AreYouShureModal)
