import React, { Component } from 'react'
import { connect } from 'react-redux'
import { setCurrentHeaderNavItem } from '../../actions/headerActions';
import { setSearchStr } from '../../actions/searchActions';
import { setTags, setCurrentTag, setUpdatingTag } from '../../actions/tagsActions';
import { setCurrentModal, setShure } from '../../actions/modalServiceActions';
import { PreloaderApp } from '../Preloader/Preloader';
import firebase from '../../firebase';
import './tagsPage.scss'

class TagsPage extends Component {
  state = {
    tagsRef: firebase.database().ref('tags'),
    tags: null
  }
  addListeners = () => {
    let loadedTags = []

    this.state.tagsRef.orderByChild('userId').equalTo(this.props.user.uid).on('child_added', snap => {
      loadedTags.push(snap.val())
      this.props.setTags(loadedTags)
      this.setState({ tags: loadedTags })
    })
    this.state.tagsRef.orderByChild('userId').equalTo(this.props.user.uid).on('value', snap => {
      
      if(!snap.val()) {
        this.props.setTags([])
        this.setState({ tags: [] })
      } else {
        let tempArr = []
        for (let key in snap.val()) {
          tempArr.push(snap.val()[key])
        }
        //loadedTags = snap.val()
        this.props.setTags(tempArr)
        this.setState({ tags: tempArr })
      }
    })
  }
  componentDidMount() {
    this.addListeners()
    this.props.setCurrentHeaderNavItem('tags')
    this.props.setSearchStr('')
    
  }
  componentWillUnmount() {
    this.props.setCurrentHeaderNavItem(null)
    this.state.tagsRef.orderByChild('userId').equalTo(this.props.user.uid).off('child_added')
    this.state.tagsRef.orderByChild('userId').equalTo(this.props.user.uid).off('value')
  }
  componentDidUpdate(prevProps) {
    if(prevProps !== this.props) {
      this.setState({ tags: this.props.tags })
    }
  }
  onClickTag = (tag) => {
    this.props.setUpdatingTag(tag)
    this.props.setCurrentModal('updateTagModal')
  }
  

  render() {
    const { tags, isLoading, setCurrentModal, searchStr } = this.props
    if(isLoading) return <PreloaderApp type={'small'}/>
    if(!tags || !tags.length) {
      return (
        <div className='tags-page'>
          <div className="tags-container">
            <div className='item add' onClick={() => setCurrentModal('addTagModal')} ><i className="fa fa-pencil-square-o" aria-hidden="true"></i></div>
          </div>
        </div>
      )
    }
    let tagsNew = [...tags]
    if(searchStr) tagsNew = tags.filter(tag => tag.name.indexOf(searchStr) + 1 > 0)
    return (
      <div className='tags-page'>
        <div className="tags-container">
          {tagsNew.map(tag => <div className='item' key={tag.id} style={{ backgroundColor: `${tag.color}` }} onClick={() => this.onClickTag(tag)}>{tag.name}</div>)}
          <div className='item add' onClick={() => setCurrentModal('addTagModal')} ><i className="fa fa-pencil-square-o" aria-hidden="true"></i></div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  tags: state.tags.tags,
  currentTag: state.tags.currentTag,
  user: state.user.currentUser,
  isLoading: state.tags.isLoading,
  searchStr: state.search.searchString
})

export default connect(mapStateToProps, { setCurrentHeaderNavItem, setSearchStr, setTags, setCurrentModal, setCurrentTag, setUpdatingTag, setShure })(TagsPage)