import React from 'react'
import Preloader from '../Preloader/Preloader'
import './basicUi.scss'
const Button = props => {
  let classes = 'button-component'
  if(props.red) classes += ' red'
  if(props.green) classes += ' green'
  if(props.black) classes += ' black'
  return (
    <div className={classes} onClick={props.onClick} style={props.style}>
      {props.loading ? <Preloader white/> : props.children}
    </div>
  )
}

export default Button

