import * as actionTypes from '../actions/types'

const initialState = {
  searchString: '',
}

export const search_reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_SEARCH_STR:
      return {
        searchString: action.payload.searchString,
      };
    default: 
      return state
  }
}

