import * as actionTypes from '../actions/types'

const initialState = {
  currentHeaderNavItem: null
}

export const header_nav_reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_CURRENT_HEADER_NAV_ITEM:
      return {
        currentHeaderNavItem: action.payload.currentHeaderNavItem,
      }
    default: 
      return state
  }
}

