import React, { Component } from 'react'
import { connect } from 'react-redux'
import SyntaxHighlighter from 'react-syntax-highlighter';
import { foundation } from 'react-syntax-highlighter/dist/styles/hljs';
import Button from '../BasicUi/Button'
import Select from 'react-select';
import { languages as langs } from '../../_services/CommonData/CommonData'
import firebase from '../../firebase';
// import brace from 'brace';
import AceEditor from 'react-ace';
import { checkChunkPageForm } from '../../_services/Validations/chunkPageValidation'
import { setCurrentModal, setShure } from '../../actions/modalServiceActions';
import 'brace/mode/javascript';
import 'brace/theme/github';

import './chunkPage.scss'
class ChunkPage extends Component {
  state = {
    chunksRef: firebase.database().ref('chunks'),
    chunk: {},
    tags: [],
    isEditing: false,
    chunkClone: {},
    nameValid: false,
    desValid: false,
    tagsValid: false,
    langValid: false,
    codeValid: false,
    isChecked: false,

  }
  componentDidMount() {
    this.setState({
      chunk: this.props.chunks.find(el => el.id === this.props.match.params.id),
      tags: this.props.tags.map(tag => {
        return { value: tag.name, label: tag.name, color: tag.color, id: tag.id }
      })
    })
  }

  editClick = () => {
    const chunkClone = { ...this.state.chunk }
    chunkClone.language = { value: chunkClone.language, label: chunkClone.language }
    this.setState({ isEditing: true, chunkClone })
  }
  setStateCurrent = (value) => {
    for (let prop in value) {
      this.setState({
        [prop]: value[prop]
      })
    }
    
  }
  saveClick = () => {
    //const chunk = 
    if (checkChunkPageForm(this.state.chunkClone, this.setStateCurrent)) {
      const chunkClone = { ...this.state.chunkClone }
      chunkClone.language = chunkClone.language.value
      this.state.chunksRef.child(`${this.state.chunk.id}`).update(chunkClone)
        .then(r => {
          console.log('chunk updated')
          const chunkNew = { ...this.state.chunkClone }
          chunkNew.language = chunkNew.language.value
          this.setState({
            isEditing: false,
            chunk: chunkNew
          })
        })
        .catch(r => { alert('error has occured') })
    }


  }
  cancelClick = () => {
    this.setState({ isEditing: false, chunkClone: {} })
  }

  // deleting 
  deleteItem = () => {
    this.state.chunksRef.child(`${this.state.chunk.id}`).remove().then(() => {
      this.props.history.push('/chunks')
    })
  }
  revert = () => { this.props.setCurrentModal(null) }
  deleteClick = () => {
    this.props.setShure(['deleteChunk', this.state.chunksRef.child(`${this.state.chunk.id}`)])
    this.props.setCurrentModal('areYouShureModal')
    // this.state.chunksRef.child(`${this.state.chunk.id}`).remove().then(() => {
    //   this.props.history.push('/chunks')
    // })


  }
  // end deleting 

  onChange = e => {
    const chunkClone = { ...this.state.chunkClone }
    chunkClone[e.target.name] = e.target.value
    this.setState({ chunkClone })
  }
  handleSelectTags = (value) => {
    const el = { ...this.state.chunkClone }
    el.tags = value
    this.setState({ chunkClone: el });
  }
  handleSelectLang = (value) => {
    const el = { ...this.state.chunkClone }
    el.language = value
    this.setState({ chunkClone: el });
  }
  handleChangeCode = (value) => {
    const el = { ...this.state.chunkClone }
    el.content = value
    this.setState({ chunkClone: el });
  }
  render() {
    if (!this.state.chunk || !this.state.chunk.tags || !this.state.chunk.tags.length) return null

    const { chunk, isEditing, chunkClone, tags, nameValid, tagsValid, desValid, codeValid, isChecked } = this.state
    return (
      <div className='chunks-page-component'>
        {/* condition for title */}
        {!isEditing ? <div className="title">{chunk.name}</div> : <input onChange={this.onChange} className={!nameValid && isChecked ? "title input wrong" : "title input"} type='text' name='name' value={chunkClone.name} />}

        {/* condition for des */}
        {!isEditing ? <div className="des">{chunk.des}</div> : <input onChange={this.onChange} className={!desValid && isChecked ? "des input wrong" : "des input "} type='text' name='des' value={chunkClone.des} />}

        {/* condition for tags */}
        {!isEditing ? <div className="tags">
          {chunk.tags.map(tag => <div key={tag.id} className="tag-item" style={{ backgroundColor: `${tag.color}` }}>{tag.label}</div>)}
        </div> : <Select
            className={!tagsValid && isChecked ? 'select tags editable wrong' : 'select tags editable '}
            name='selectedTags'
            isMulti value={chunkClone.tags}
            onChange={this.handleSelectTags}
            options={tags}
            theme={(theme) => ({
              ...theme,
              colors: {
                ...theme.colors,
                primary25: '#688FB2',
                primary: '#688FB2',
              },
            })}
          />}

        {/* condition for code (with lang) */}
        {!isEditing ? <div className="code">
          <div className="lang">{chunk.language}</div>
          <SyntaxHighlighter language={chunk.language.value} style={foundation}>{chunk.content}</SyntaxHighlighter>
        </div> :
          <React.Fragment>
            <Select
              className='select lang'
              name='selectedLang'
              value={chunkClone.language}
              onChange={this.handleSelectLang}
              options={langs}
              theme={(theme) => ({
                ...theme,
                colors: {
                  ...theme.colors,
                  primary25: '#688FB2',
                  primary: '#688FB2',
                },
              })}
            />
            <AceEditor
              style={{ marginTop: '20px' }}
              className={!codeValid && isChecked ? 'ace-editor wrong' : 'ace-editor '}
              mode={chunkClone.language.value}
              theme="github"
              width='100%'
              maxLines='Infinity'
              value={chunkClone.content}
              onChange={this.handleChangeCode}
              name="UNIQUE_ID_OF_DIV"
              editorProps={{ $blockScrolling: false }}
            />
          </React.Fragment>}

        {/* condition for buttons */}
        {!isEditing ? 
          <React.Fragment>
            <Button onClick={this.editClick} red >Edit</Button> 
            <Button onClick={this.deleteClick} black>Delete</Button>
          </React.Fragment> :
          <div className='apply-buttons'>
            <Button onClick={this.saveClick} green >Save</Button>
            <Button onClick={this.cancelClick} red >Cancel changes</Button>
          </div>
        }
      </div>
    )
  }
}
const mapStateToProps = state => ({
  chunks: state.chunks.chunks,
  tags: state.tags.tags
})
export default connect(mapStateToProps, { setCurrentModal, setShure })(ChunkPage)
