import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { setCurrentHeaderNavItem } from '../../actions/headerActions'
import Tags from '../Tags/Tags'
import { connect } from 'react-redux'
import { setChunks } from '../../actions/chunksActions'
import firebase from '../../firebase';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/styles/hljs';
import { setCurrentModal } from '../../actions/modalServiceActions';
import { setCurrentTag } from '../../actions/tagsActions';
import { setSearchStr } from '../../actions/searchActions';
import './chunks.scss'

class Chunks extends Component {
  state = {
    chunksRef: firebase.database().ref('chunks'),
  }
  
  addListeners = () => {
    let loadedChunks = []

    this.state.chunksRef.orderByChild('userId').equalTo(this.props.user.uid).on('child_added', snap => {
      loadedChunks.unshift(snap.val())
      this.props.setChunks(loadedChunks)
      this.setState({ chunks: loadedChunks })
    })
    this.state.chunksRef.orderByChild('userId').equalTo(this.props.user.uid).on('value', snap => {
      if(!snap.val()) {
        this.props.setChunks([])
        this.setState({ chunks: [] })
      }
    })
  }
  componentDidUpdate(prevProps) {
    if(prevProps !== this.props) {
      this.setState({ chunks: this.props.chunks })
    }
  }
  componentDidMount() {
    this.props.setCurrentHeaderNavItem('chunks')
    this.props.setSearchStr('')
    this.addListeners()
  }
  componentWillUnmount() {
    this.props.setCurrentHeaderNavItem(null)  
    this.state.chunksRef.orderByChild('userId').equalTo(this.props.user.uid).off('child_added')
    this.state.chunksRef.orderByChild('userId').equalTo(this.props.user.uid).off('value')
  }

  render() {
    const { chunks } = this.state
    const { setCurrentModal, currentTag, searchStr, setCurrentTag } = this.props
    
    return (
      <div className='chunks-component'>
        <Tags />
        <div className="chunks-container">
          {/* {!chunks || chunks.isLoading ? <PreloaderApp type={'small'}/> : <RenderChunkItems chunks={chunks}/>} */}
          <RenderChunkItems chunks={chunks} currentTag={currentTag} searchStr={searchStr} setCurrentTag={setCurrentTag} />
          {!chunks || chunks.isLoading ? null : <div className='chunk-item new-chunk' onClick={() => setCurrentModal('addChunkModal')}>
            <i className="fa fa-pencil-square-o" aria-hidden="true"></i>
          </div>}
        </div>
        
      </div>
    )
  }
}

const RenderChunkItems = ({ chunks, currentTag, searchStr, setCurrentTag }) => {
  if(!chunks) return null

  let filteredChunks = []

  // filtering by tag
  currentTag.value === 'all' ? filteredChunks = chunks : filteredChunks = chunks.filter(el => el.tags.find(tag => tag.id === currentTag.id))

  // filtering by search string
  if(searchStr)
    filteredChunks = filteredChunks.filter(chunk => chunk.name.indexOf(searchStr) + 1 > 0)

  return filteredChunks.map(chunk => chunk.tags && <div className='chunk-item' key={chunk.id}>
    <div className="heading">
      <Link to={`/chunks/${chunk.id}`} className="title">{chunk.name}</Link>
      <div className="des">{chunk.des}</div>
      <div className="tags">
        {chunk.tags.map(tag => <div key={tag.id} onClick={() => setCurrentTag({id: tag.id, value: tag.name})} className="item" style={{ backgroundColor: `${tag.color}` }}>{tag.label}</div>)}
      </div>
      <div className="language">{chunk.language}</div>
    </div>
    <div className="code">
      <SyntaxHighlighter language={chunk.language} style={docco}>{chunk.content}</SyntaxHighlighter>
    </div>
  </div>)
}

const mapStateToProps = state => ({
  chunks: state.chunks.chunks,
  user: state.user.currentUser,
  currentTag: state.tags.currentTag,
  searchStr: state.search.searchString
})

export default connect((mapStateToProps), { setCurrentHeaderNavItem, setChunks, setCurrentModal, setCurrentTag, setSearchStr })(Chunks)
