import * as actionTypes from './types'

export const setCurrentModal = modal => {
  return {
    type: actionTypes.SET_CURRENT_MODAL,
    payload: {
      currentModal: modal
    }
  }
}
export const setShure = data => {
  return {
    type: actionTypes.SET_SHURE,
    payload: {
      shureData: data
    }
  }
}
