import firebase from '../../firebase'
const _chunksRef = firebase.database().ref('chunks')

export const addChunk = (name, des, language, code, tags, userId, setCurrentModal) => {
  const chunksRef = _chunksRef
  const key = _chunksRef.push().key
  const newChunk = {
    id: key,
    name: name,
    des: des,
    userId: userId,
    language: language,
    content: code,
    tags: tags

  }
  chunksRef.child(key).update(newChunk).then(() => {
    console.log('chunk created')
    setCurrentModal(null)
  }).catch(error => { console.log(error) })
}