import React, { Component } from "react";
import { connect } from "react-redux";
import firebase from '../../firebase';
import { setTags, setCurrentTag } from '../../actions/tagsActions';
import { setCurrentModal } from '../../actions/modalServiceActions';
import { PreloaderApp } from '../Preloader/Preloader';
import './tags.scss'

class Tags extends Component {
  state = {
    tagsRef: firebase.database().ref('tags'),
  }
  
  addListeners = () => {
    let loadedTags = []

    this.state.tagsRef.orderByChild('userId').equalTo(this.props.user.uid).on('child_added', snap => {
      loadedTags.push(snap.val())
      this.props.setTags(loadedTags)
      this.setState({ tags: loadedTags })
    })
    this.state.tagsRef.orderByChild('userId').equalTo(this.props.user.uid).on('value', snap => {
      if(!snap.val()) {
        this.props.setTags([])
        this.setState({ tags: [] })
      }
    })
  }
  componentDidMount() {
    this.addListeners()
  }
  componentWillUnmount(){
    this.state.tagsRef.orderByChild('userId').equalTo(this.props.user.uid).off('child_added')
    this.state.tagsRef.orderByChild('userId').equalTo(this.props.user.uid).off('value')
  }
  componentDidUpdate(prevProps) {
    if(prevProps !== this.props) {
      this.setState({ tags: this.props.tags })
    }
  }
  onClickTag = (tag, type) => {
    if(type === 'all') {
      this.props.setCurrentTag({
        id: null,
        value: 'all'
      })
      return
    }
    this.props.setCurrentTag({
      id: tag.id,
      value: tag.name
    })
  }

  render() {
    const { isLoading, setCurrentModal, currentTag } = this.props
    if(isLoading) return <PreloaderApp type={'small'}/>
    return (
      
      <div className="tags-component">
        <div className={currentTag.value === 'all' ? "tag-item all selected" : "tag-item all"} onClick={() => this.onClickTag(null, 'all')}>ALL</div>
        {this.props.tags.map(tag => {
          return (
            <div onClick={() => this.onClickTag(tag)} className={currentTag.id === tag.id ? "tag-item selected" : "tag-item"} key={tag.id} style={{ backgroundColor: `${tag.color}` }}>
              {tag.name}
            </div>
          )
        })}
        <div className="tag-item add" onClick={() => setCurrentModal('addTagModal')}><i className="fa fa-plus" aria-hidden="true"></i></div>
        
      </div>
    )
  }
}

const mapStateToProps = state => ({
  tags: state.tags.tags,
  currentTag: state.tags.currentTag,
  user: state.user.currentUser,
  isLoading: state.tags.isLoading
});

export default connect(mapStateToProps, { setTags, setCurrentModal, setCurrentTag })(Tags);
