import React, { Component } from 'react'
import Preloader from '../../../components/Preloader/Preloader'
import { checkAddChunkForm } from '../../Validations/chunksValidation'
import { addChunk } from '../../FirebaseService/chunksMethods'
import { connect } from 'react-redux'
import { setCurrentModal } from '../../../actions/modalServiceActions'
import Textarea from 'react-textarea-autosize';
import Select from 'react-select';
import { languages } from '../../CommonData/CommonData'



class AddChunkModal extends Component {
  state = {
    name: { value: '', isValid: false },
    des: { value: '', isValid: false },
    language: { value: '', isValid: false },
    code: { value: '', isValid: false },
    tags: [],
    selectedTags: { value: [], isValid: false },
    errors: [],
    loading: false,
    checked: false,
  }


  handleChange = e => {
    const el = { ...this.state[e.target.name] }
    el.value = e.target.value
    this.setState({ [e.target.name]: el })
  }
  handleChangeSelect = (value, type) => {
    const el = { ...this.state[type] }
    el.value = value
    this.setState({ [type]: el });
  }
  handleChangeSelectLang = (lang) => {
    this.handleChangeSelect(lang, 'language')
  }
  handleChangeSelectTags = (tags) => {
    this.handleChangeSelect(tags, 'selectedTags')
  }

  setStateCurrent = (propName, value) => {
    this.setState({
      [propName]: value
    })
  }
  
  handleSubmit = e => {
    e.preventDefault()
    if (checkAddChunkForm(this.state, this.setStateCurrent)) {
      const { name, des, language, code, selectedTags } = this.state
      const { user, setCurrentModal } = this.props
      addChunk(name.value, des.value, language.value.value, code.value, selectedTags.value, user.uid, setCurrentModal)
    }
      
  }
  componentDidMount() {
      const initialTag = this.props.tags.find(el => el.id === this.props.currentTag.id) || null
      this.setState({
        tags: this.props.tags.map(tag => {
          return {value: tag.name, label: tag.name, color: tag.color, id: tag.id}
        }),
        selectedTags: {value: initialTag ? [{color: initialTag.color, id: initialTag.id, label: initialTag.name, value: initialTag.name}] : [], isValid: false}
      })
    
  }

  render() {
    const { name, des, language, code, tags, selectedTags, errors, checked, loading } = this.state
    return (
      <div className="formblock add-tag add-chunk">
        <div className="title">Add new Chunk</div>

        <form onSubmit={this.handleSubmit}>
          <div className="inputs">
            <div className="item">
              <div className="icon"><i className="fa fa-ravelry" aria-hidden="true"></i></div>
              <input autoComplete='off' className={name.isValid || !checked ? '' : 'wrong'} onChange={this.handleChange} value={name.value} type="text" placeholder='Name' name='name' />
            </div>
            <div className="item">
              <div className="icon"><i className="fa fa-info" aria-hidden="true" ></i></div>
              <input autoComplete='off' className={des.isValid || !checked ? '' : 'wrong'}  onChange={this.handleChange} value={des.value} type="text" placeholder='Description' name='des' />
            </div>
            <div className="item">
              <div className="icon"><i className="fa fa-file-code-o" aria-hidden="true" ></i></div>
              <input  value='Language' type="text" disabled className={language.isValid || !checked ? '' : 'wrong'} />

              <Select 
                className='select' 
                name='language' 
                value={language.value} 
                onChange={this.handleChangeSelectLang} 
                options={languages}
                theme={(theme) => ({
                  ...theme,
                  colors: {
                  ...theme.colors,
                    primary25: '#688FB2',
                    primary: '#688FB2',
                  },
                })}
              />
            </div>
            <div className="item">
              <div className="icon"><i className="fa fa-hashtag" aria-hidden="true" ></i></div>
              <input  value='Tags' type="text" disabled className={selectedTags.isValid || !checked ? '' : 'wrong'} />

              <Select 
                className='select' 
                name='selectedTags' 
                isMulti value={selectedTags.value} 
                onChange={this.handleChangeSelectTags} 
                options={tags}
                theme={(theme) => ({
                  ...theme,
                  colors: {
                  ...theme.colors,
                    primary25: '#688FB2',
                    primary: '#688FB2',
                  },
                })}
              />
            </div>
            <div className="item">
              <div className="icon"><i className="fa fa-code" aria-hidden="true"></i></div>
              <Textarea autoComplete='off' className={code.isValid || !checked ? '' : 'wrong'}  onChange={this.handleChange} value={code.value} placeholder='Code' name='code' />
              
            </div>
            
            
           
          </div>

          {errors.length > 0 ? <div className='errors'>{errors.map((el,idx) => <div className='item' key={`${el}+${idx}`}> -- {el}-- </div>)}</div> : null}
          

          <button disabled={loading} type="submit" className="btn-1">{loading ? <Preloader white/> : 'Add'}</button>

        </form>
      </div>
    )
  }
}
const mapStateToProps = state => ({
  user: state.user.currentUser,
  tags: state.tags.tags,
  currentTag: state.tags.currentTag
})
export default connect(mapStateToProps, {setCurrentModal})(AddChunkModal)