import firebase from '../../firebase'
const _tagsRef = firebase.database().ref('tags')
const _chunksRef = firebase.database().ref('chunks')

export const addTag = (name, color, userId, setCurrentModal) => {
  const tagsRef = _tagsRef
  const key = tagsRef.push().key
  const newTag = {
    id: key,
    name: name,
    color: color,
    userId: userId

  }
  tagsRef.child(key).update(newTag).then(() => {
    console.log('tag created')
    setCurrentModal(null)
  }).catch(error => { console.log(error) })
}
export const updateTag = (name, color, userId, setCurrentModal, id, filteredChunks, isRemove) => {
  const tagsRef = _tagsRef
  const chunks = firebase.database().ref('chunks')
  const newTag = {
    id: id,
    name: name,
    color: color,
    userId: userId

  }
  if (isRemove) {
    tagsRef.child(`${id}`).remove().then(() => {
      console.log('tag deleted')
      setCurrentModal(null)
      filteredChunks.forEach(el => {
        _chunksRef.child(`${el.id}`).update(el)
          .then((snap) => {
            console.log('chunk updated', snap)
            
          })
          .catch(error => { console.log(error) })
      })


    }).catch(error => { console.log(error) })
  } else {
    tagsRef.child(`${id}`).update(newTag).then(() => {
      console.log('tag updated')
      setCurrentModal(null)
      filteredChunks.forEach(el => {
        _chunksRef.child(`${el.id}`).update(el)
          .then((snap) => {
            console.log('chunk updated', snap)
            //setCurrentModal(null)
          })
          .catch(error => { console.log(error) })
      })


    }).catch(error => { console.log(error) })
  }

}