import * as actionTypes from '../actions/types'

const initialState = {
  chunks: [],
  isLoading: true
}

export const chunks_reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_CHUNKS:
      return {
        chunks: action.payload.chunks,
        isLoading: false
      }
    default: 
      return state
  }
}

