import React, { Component } from 'react'
import * as Modals from './Modals'
import { connect } from 'react-redux'
import { setCurrentModal } from '../../actions/modalServiceActions'
import './modalService.scss'

class ModalService extends Component {
  render() {
    const { currentModal, history } = this.props
    if (!this.props.currentModal)
      return null
    const Modal = Modals[currentModal]
    return (
      <div className="modal-wrapper">
        <div className="modal-overlay" onClick={() => this.props.setCurrentModal(null)}></div>
        <div className="modal-content">
          <div className="close-button" onClick={() => this.props.setCurrentModal(null)}><i className="fa fa-times" aria-hidden="true"></i></div>
          {<Modal history={history}/>}
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  currentModal: state.modal_service.currentModal,
  functions: state.modal_service.functions
})

export default connect(mapStateToProps, { setCurrentModal })(ModalService)